/**
 * Created by Administrator on 2017/4/13.
 */
$(function () {
    var levelArr = [
        {leverl: 0, minute: 0},
        {leverl: 1, minute: 60},
        {leverl: 2, minute: 120},
        {leverl: 3, minute: 180},
        {leverl: 4, minute: 300},
        {leverl: 5, minute: 600},
        {leverl: 6, minute: 1000},
        {leverl: 7, minute: 1500},
        {leverl: 8, minute: 2000},
        {leverl: 9, minute: 2500},
        {leverl: 10, minute: 3500},
        {leverl: 11, minute: 5000},
        {leverl: 12, minute: 7500},
        {leverl: 13, minute: 10000}
    ];
    var imgArr = [
        {img: "//3gimg.qq.com/wspbbs/pace/webview/dm/images/image_number/0.png"},
        {img: "//3gimg.qq.com/wspbbs/pace/webview/dm/images/image_number/1.png"},
        {img: "//3gimg.qq.com/wspbbs/pace/webview/dm/images/image_number/2.png"},
        {img: "//3gimg.qq.com/wspbbs/pace/webview/dm/images/image_number/3.png"},
        {img: "//3gimg.qq.com/wspbbs/pace/webview/dm/images/image_number/4.png"},
        {img: "//3gimg.qq.com/wspbbs/pace/webview/dm/images/image_number/5.png"},
        {img: "//3gimg.qq.com/wspbbs/pace/webview/dm/images/image_number/6.png"},
        {img: "//3gimg.qq.com/wspbbs/pace/webview/dm/images/image_number/7.png"},
        {img: "//3gimg.qq.com/wspbbs/pace/webview/dm/images/image_number/8.png"},
        {img: "//3gimg.qq.com/wspbbs/pace/webview/dm/images/image_number/9.png"},
        {img: "//3gimg.qq.com/wspbbs/pace/webview/dm/images/image_number/10.png"},
        {img: "//3gimg.qq.com/wspbbs/pace/webview/dm/images/image_number/11.png"},
        {img: "//3gimg.qq.com/wspbbs/pace/webview/dm/images/image_number/12.png"},
        {img: "//3gimg.qq.com/wspbbs/pace/webview/dm/images/image/level-13@4x.png"}
    ];
    var minute = GetQueryString("minute");
    var language = GetQueryString("language");
    var en_time = "Total exercise";
    var en_minute = "mins";
    var ch_time = "累计运动时长";
    var ch_minute = "分钟";
    var zh_tw_time = "累計運動時長";
    var zh_tw_minute = "分鐘";
    var ch_duration = "到达";
    var en_duration = "also takes";
    var zh_tw_duration = "到達";
    var ch_need = "还需要";
    var zh_tw_need = "還需要";
    var zh_tw_first = "你已達到最高級";
    var ch_first = "你已达到最高级";
    minute = Math.round(minute);
    isNaN(minute) ? minute = 0 : minute ? minute < 0 ? minute = 0 : minute = Number(minute) : minute;
    translate(language);
    function translate(lan) {
        if (lan != null) {
            switch (lan) {
                case "zh_cn":
                    durationHtml(ch_time, ch_minute);
                    durationJudge(minute, lan);
                    break;
                case "en":
                    durationHtml(en_time, en_minute);
                    durationJudge(minute, lan);
                    break;
                case "zh_hk":
                case "zh_tw":
                    durationHtml(zh_tw_time, zh_tw_minute);
                    durationJudge(minute, lan);
                    break;
            }
        } else {
            durationHtml(ch_time, ch_minute);
            durationJudge(minute, lan);
        }

    }

//頁面加載
    function durationHtml(total, lan_mins) {
        for (var i = 1; i < levelArr.length - 2; i += 2) {
            var html = "<li class='levelTime level'><div class='time_r'></div>" +
                "<div class='level_run'><div class='lv_css'>LV." + levelArr[i].leverl + "</div>" +
                "<div class='level_css'>" + total + "<span class='level_font'>&nbsp;" + levelArr[i].minute + "</span>&nbsp;" + lan_mins + "</div> </div></li>" +
                "<li class='levelTime level'> <div class='time_r'></div>" +
                "<div class='level_run_left'> <div class='lv_css'>LV." + levelArr[i + 1].leverl +
                "</div><div class='level_css'>" + total + "<span class='level_font'>&nbsp;" + levelArr[i + 1].minute +
                "&nbsp;</span>" + lan_mins + "</div> </div> </li>";
            $("#levelClassify").append(html);
        }
        var html_13 = "<li class=\"levelTime level\"> <div class=\"time_r\"></div><div class=\"level_13\"> <span class='lv_13'>LV." +
            levelArr[levelArr.length - 1].leverl + "</span> <div class='lv_css13 level_css'>" + total + "<span class='level_font'>&nbsp;" + levelArr[levelArr.length - 1].minute + "&nbsp;</span>" + lan_mins + "</div> </div> </li>";
        $("#levelClassify").append(html_13);
    }

//图片加载
    function levelimgNum(level) {
        if (level < levelArr.length - 1) {
            var img_html = "<div class=\"levelimgNum\">" +
                "<img class=\"imgNum\" src=\"" + imgArr[level].img + "\"></div>"
            $("#level").append(img_html)
        }
    }

    function levelImg(level) {
        var img_html = "";
        switch (level) {
            case 0:
            case 1:
            case 2:
            case 3:
            case 4:
                img_html = "<img class=\"levelImg\" src=//3gimg.qq.com/wspbbs/pace/webview/dm/images/image_boder/level-0@4x.png>";
                $("#level").append(img_html);
                break;
            case 5:
            case 6:
            case 7:
            case 8:
            case 9:
                img_html = "<img class=\"levelImg\" src=//3gimg.qq.com/wspbbs/pace/webview/dm/images/image_boder/level-5@4x.png>";
                $("#level").append(img_html);
                break;
            case 10:
            case 11:
            case 12:
                img_html = "<img class=\"levelImg\" src=//3gimg.qq.com/wspbbs/pace/webview/dm/images/image_boder/level-9@4x.png>";
                $("#level").append(img_html);
            case 13:
                $(".levelScore").before("<img src=\"" + imgArr[levelArr.length - 1].img + "\">")
        }
    }

//参数
    function GetQueryString(name) {
        var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
        var r = window.location.search.substr(1).match(reg);
        if (r != null)return unescape(r[2]);
        return null;
    }

//显示隐藏
    function levelShow(level) {
        if (level < 13) {
            $("#level_last").hide();
            $(".levelTime").show();
            $("#levelClassify").show();
            $("#level").show();
        } else if (level) {
            $(".levelTime").hide();
            $("#level_last").show();
            $("#levelClassify").hide();
            $("#level").hide();
        }
    }

    function displayPage(level, pageMinute, duration, need, time, sports_time, first_html) {
        levelShow(level);
        $("#level>img").eq(level).addClass("block");
        if (level < 13) {
            $(".levelTime_span").html(duration + "&nbsp;<span class='level_font'>LV." + (level + 1) + "</span>&nbsp;" + need + "&nbsp;" + "<span class='level_font'>" + pageMinute + "</span>" + "&nbsp;" + time);
        } else {
            $(".levelScore").html(first_html);
            $("#scores").html(sports_time + "&nbsp;<span class='level_font'>" + pageMinute + "</span>&nbsp;" + time);
        }
        for (var i = 0; i < level; i++) {
            $("ul>li").eq(i).hide();
        }
    }

//英文
    function displayPage_en(level, pageMinute) {
        levelShow(level);
        $("#level>img").eq(level).addClass("block");
        if (level < 13) {
            $(".levelTime_span").html("<span class='level_font'>LV." + (level + 1) + "&nbsp;also takes  </span>&nbsp;" + "<span class='level_font'>" + pageMinute + "</span>" + "&nbsp;mins");
        } else {
            $(".levelScore").html("Is already the highest Lv");
            $("#scores").html("Total exercise&nbsp;<span class='level_font'>" + pageMinute + "</span>&nbsp;mins");
        }
        for (var i = 0; i < level; i++) {
            $("ul>li").eq(i).hide();
        }
    }

    function durationJudge(curMinute, lan) {
        if (curMinute < levelArr[levelArr.length - 1].minute) {
            var levelLast = 0;
            var curLevel = 0;
            for (var i = 0; i < levelArr.length; i++) {
                var item = levelArr[i];
                curLevel = item.leverl;
                if (curMinute >= item.minute) {
                    continue;
                } else {
                    curLevel
                    break;
                }
            }
            levelLast = levelArr[curLevel].minute - curMinute;
            curLevel--;
        } else {
            curLevel = levelArr[levelArr.length - 1].leverl;
            levelLast = curMinute;
        }
        if (lan != null) {
            switch (lan) {
                case "zh_cn":
                    displayPage(curLevel, levelLast, ch_duration, ch_need, ch_minute, ch_time, ch_first);
                    break;
                case "en":
                    displayPage_en(curLevel, levelLast);
                    break;
                case "zh_hk":
                case "zh_tw":
                    displayPage(curLevel, levelLast, zh_tw_duration, zh_tw_need, zh_tw_minute, zh_tw_time, zh_tw_first);
                    break;
            }
        } else {
            displayPage(curLevel, levelLast, ch_duration, ch_need, ch_minute, ch_time, ch_first);
        }
        levelImg(curLevel);
        levelimgNum(curLevel)
    }
})




